html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  font-family: -apple-system,
  BlinkMacSystemFont,
  'Segoe UI',
  Roboto,
  Oxygen-Sans,
  Ubuntu,
  Cantarell,
  'Helvetica Neue',
  sans-serif,
  'Apple Color Emoji',
  'Segoe UI Emoji',
  'Segoe UI Symbol';
  color: red;

  background-image: url('./images/world-map.png');
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  

  
   
}



ul {
  list-style: none;
}

.several-counrty-name {
  text-align: center;
}

#country-input {
  
  display: block;
  margin: 0 auto;
  height: 24px;
  min-width: 300px;
  margin-top: 15px;
  
}

.flex {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 20px;
}

.country-name {
  font-size: 28px;
  text-align: center;
}

.country-flag {
  border: 1px solid gray;
  margin-left: 15px;
}